import GetUrl from '../server/ApiEndpoints'
let bearerToken = 'Bearer ' + localStorage.getItem('HDI-token')

const OrganizationsAdd = async function (addObject, setToastMessage) {
    return fetch(GetUrl('CentersAdd'), {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            'Authorization': bearerToken,
            'Accept': 'application/json',
        },
        body: JSON.stringify(addObject)
    })
        .then(response => {
            return response
        })
        .catch(error => {
            console.log(error)
            setToastMessage([
                { title: 'Error!', description: 'An error occured while processing the request', type: 'danger' }
            ])
        })
}
const OrganizationsList = async function () {
    return await fetch(GetUrl('CentersList'), {
        method: 'GET',
        mode: 'cors',
        headers: {
            'Authorization': bearerToken,
            'Accept': 'application/json',
        }
    })
        .catch(error => {
            console.log(error.message)
        })
}

const OrganizationsDetail = async function (id) {
    return await fetch(GetUrl('CentersList') + '/' + id, {
        method: 'GET',
        mode: 'cors',
        headers: {
            'Authorization': bearerToken,
            'Accept': 'application/json',
        }
    })
        .catch(error => {
            console.log(error.message)
        })
}

const OrganizationsEdit = async function (id, editObject, setToastMessage) {
    return fetch(GetUrl('CentersEdit') + '/' + id, {
        method: 'PATCH',
        mode: 'cors',
        headers: {
            'Content-Type': 'application/json',
            'Authorization': bearerToken,
            'Accept': 'application/json'
        },
        body: JSON.stringify(editObject)
    })
        .then(response => {
            return response
        })
        .catch(error => {
            console.log(error)
            setToastMessage([
                { title: 'Error!', description: 'An error occured while processing the request', type: 'danger' }
            ])
        })
}


export { OrganizationsList, OrganizationsEdit, OrganizationsDetail, OrganizationsAdd }