import GetUrl from '../server/ApiEndpoints'
import AWS from 'aws-sdk'

const ID = 'AKIAWBK3UNFZVVF7OF4B';
const SECRET = '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';
const BUCKET_NAME = 'indigenous-images';
const s3 = new AWS.S3({
    accessKeyId: ID,
    secretAccessKey: SECRET
});
let bearerToken = 'Bearer ' + localStorage.getItem('HDI-token')

const MediasAdd = async function (addObject, setToastMessage) {
    return fetch(GetUrl('MediasAdd'), {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            'Authorization': bearerToken,
            'Accept': 'application/json',
        },
        body: JSON.stringify(addObject)
    })
        .then(response => {
            return response
        })
        .catch(error => {
            console.log(error)
            setToastMessage([
                { title: 'Error!', description: 'An error occured while processing the request', type: 'danger' }
            ])
        })
}

const UploadMedia = async function (fileContent, setToastMessage, callback) {
    const params = {
        Bucket: BUCKET_NAME,
        Key: Math.floor(Math.random() * 10000000000).toString(),
        Body: fileContent,
        ContentType: 'image/jpeg',
        ACL: 'public-read-write'
    };

    s3.upload(params, async function (err, data) {
        if (err) {
            setToastMessage([
                { title: 'Error!', description: 'An error occured while processing the request: ' + err, type: 'danger' }
            ])
        }
        console.log(`File uploaded successfully.`, data);
        let response = await MediasAdd({ type: 'image', extension: '.' + fileContent.name.split('.')[1], path: data.Location }, setToastMessage)
        if (response.ok) {
            let jsonResult = await response.json()
            await callback(jsonResult.data._id)
        }
        else {
            await callback(-1)
            setToastMessage([
                { title: 'Error!', description: 'An error occured while processing the request: ' + err, type: 'danger' }
            ])
        }
    })
}

const UploadManyMedias = async function (fileContents, setToastMessage, callback) {
    let promises = []
    fileContents.forEach(fileContent => {
        const params = {
            Bucket: BUCKET_NAME,
            Key: Math.floor(Math.random() * 10000000000).toString(),
            Body: fileContent.content,
            ContentType: 'image/jpeg',
            ACL: 'public-read-write'
        };
        promises.push({ type: fileContent.type, promise: s3.upload(params).promise() })
    })
    Promise.all(promises.map(pr => pr.promise)).then(async function (results) {
        let mediaIds = []
        for (let i = 0; i < results.length; i++) {
            console.log(`File uploaded successfully.`, results[i]);
            if (fileContents[i].type !== 'icon') {
                let response = await MediasAdd({ type: 'image', extension: '.jpg', path: results[i].Location }, setToastMessage)
                if (response.ok) {
                    let jsonResult = await response.json()
                    mediaIds.push(jsonResult.data._id)
                }
                else {
                    setToastMessage([
                        { title: 'Error!', description: 'An error occured while processing the request', type: 'danger' }
                    ])
                    return
                }
            }
            else
                mediaIds.push(results[i].Location)
        }
        await callback(mediaIds)
    });
}

export { UploadMedia, UploadManyMedias }